.transactionHistory {
    width: 800px;
    border: 1px solid teal;
    border-radius: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    color: rgb(0, 0, 0);
    background-color: rgb(215, 239, 250);
    margin-left: auto;
    margin-right: auto;
  }
  
  .title {
    font-size: 25px;
    font-weight: 900px;
    font-style: italic;
    color: rgb(0, 0, 0);
  }
  
  .tr {
    gap: 10px;
  }
  
  .td {
    width: 200px;
    height: 40px;
    text-align: center;
    border-bottom: 1px solid lightcoral;
    color: teal;
  }
  
  .tt {
    color: lightslategray;
    width: 200px;
    height: 40px;
    text-align: center;
    border-bottom: 1px solid lightcoral;
    border-left: 1px solid lightcoral;
    border-right: 1px solid lightcoral;
  }